<script lang="ts">
export default {
  data() {
    return {
      autoPlayTopTimer: null,
      autoPlayInterval: 6000,
      topIndex: 3,
      topMax: 3,
      serviceActiveIndex: 0,
      config: {
        registerPath: "/#/register"
      }
    };
  },
  computed: {
    serviceActiveBgImg() {
      return `/home/indexproductbg0${this.serviceActiveIndex + 1}.jpg`;
    }
  },
  methods: {
    autoPlayTop() {
      this.autoPlayTopTimer = setInterval(() => {
        this.topIndex++;
        if (this.topIndex === this.topMax) {
          this.topIndex = 0;
        }
      }, this.autoPlayInterval);
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.topIndex = 0;
      this.autoPlayTop();
    });
  },
  beforeDestroy() {
    if (this.autoPlayTopTimer) {
      window.clearInterval(this.autoPlayTopTimer);
    }
  }
};
</script>
